.App {
  text-align: center;
  background-color: #0f182e;
  height: 100vh
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color:#0f182e;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: EFC06A;
}

.App-link {
  color: #DF9735;
}

.Main{
  background-color: #0f182e;

}
.Body{
  background-color: #0f182e;
  padding: 0;
  margin: 0;
  min-height: 100%;
}
.Colors{
 color: #F3ECE3
#6A7282
#293942
#DF9735

}



@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
